import React from 'react'
import { nullToNA } from '../../../../Components/Common/PowerUps/PowerupFunctions'

function ReviewMovableApplication(props) {

    const { setFormIndex, showLoader, collectFormDataFun, toastFun } = props?.values
    let labelStyle = "mt-6 -ml-7 text-md text-gray-600  font-bold"
    let inputStyle = "mt-6 -ml-7 text-sm text-gray-600"

    const submitForm = () => {
        props.submitFun()
    }
    console.log("data in review form...", props?.reviewIdNameData)
    console.log("select data in review form ...", props?.reviewIdNameData)

    return (
        <>

            <div className='absolute w-full ml-4 top-4 pb-24'>
                <div className='grid grid-cols-1 md:grid-cols-12 lg:grid-cols-12 gap-4 container mx-auto  '>
                    <div className='col-span-8 border border-dashed border-violet-800 '>
                        <h1 className='text-center p-3 mb-2 text-lg bg-white text-gray-600 font-sans font-bold'>Application Details</h1>
                        <div class="flex flex-wrap flex-row -mx-4 ml-12">
                            <div class="flex-shrink max-w-full px-4 w-full md:w-1/2 lg:w-1/4">
                                <p className={`${labelStyle}`}>License From</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.movableVehicle?.licenseFrom || props?.reviewIdNameData?.movableVehicle?.license_from)}</span>
                            </div>
                            <div class="flex-shrink max-w-full px-4 w-full md:w-1/2 lg:w-1/4">
                                <p className={`${labelStyle}`}>License To  </p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.movableVehicle?.licenseTo || props?.reviewIdNameData?.movableVehicle?.license_to)}</span>
                            </div>
                            <div class="flex-shrink max-w-full px-4 w-full md:w-1/2 lg:w-1/4">
                                <p className={`${labelStyle}`}>Applicant</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.movableVehicle?.applicantName || props?.reviewIdNameData?.movableVehicle?.applicant)}</span>
                            </div>
                            <div class="flex-shrink max-w-full px-4 w-full md:w-1/2 lg:w-1/4">
                                <p className={`${labelStyle}`}>Residence Address</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.movableVehicle?.residenceAddress || props?.reviewIdNameData?.movableVehicle?.residence_address)}</span>
                            </div>
                        </div>

                        <div class="flex flex-wrap flex-row -mx-4 ml-12">
                            <div class="flex-shrink max-w-full px-4 w-full md:w-1/2 lg:w-1/4">
                                <p className={`${labelStyle}`}>Residence Ward No </p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.movableVehicle?.residenceWardNo || props?.reviewIdNameData?.movableVehicle?.ward_name)}</span>
                            </div>
                            <div class="flex-shrink max-w-full px-4 w-full md:w-1/2 lg:w-1/4">
                                <p className={`${labelStyle}`}>Permanent Address</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.movableVehicle?.permanentAddress || props?.reviewIdNameData?.movableVehicle?.permanent_address)}</span>
                            </div>
                            <div class="flex-shrink max-w-full px-4 w-full md:w-1/2 lg:w-1/4">
                                <p className={`${labelStyle}`}>Permanent Ward No</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.movableVehicle?.permanentWardNo || props?.reviewIdNameData?.movableVehicle?.permanent_ward_name)}</span>
                            </div>
                            <div class="flex-shrink max-w-full px-4 w-full md:w-1/2 lg:w-1/4">
                                <p className={`${labelStyle}`}>Father Name</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.movableVehicle?.fatherName || props?.reviewIdNameData?.movableVehicle?.father)}</span>
                            </div>
                        </div>

                        <div class="flex flex-wrap flex-row -mx-4 ml-12">
                            <div class="flex-shrink max-w-full px-4 w-full md:w-1/2 lg:w-1/4">
                                <p className={`${labelStyle}`}>Aadhar No</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.movableVehicle?.aadharNo || props?.reviewIdNameData?.movableVehicle?.aadhar_no)}</span>
                            </div>
                            <div class="flex-shrink max-w-full px-4 w-full md:w-1/2 lg:w-1/4">
                                <p className={`${labelStyle}`}>E-mail </p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.movableVehicle?.email || props?.reviewIdNameData?.movableVehicle?.email)}</span>
                            </div>
                            <div class="flex-shrink max-w-full px-4 w-full md:w-1/2 lg:w-1/4">
                                <p className={`${labelStyle}`}>Mobile No. </p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.movableVehicle?.mobileNo || props?.reviewIdNameData?.movableVehicle?.mobile_no)}</span>
                            </div>
                            <div class="flex-shrink max-w-full px-4 w-full md:w-1/2 lg:w-1/4">
                                <p className={`${labelStyle}`}>Entity Name</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.movableVehicle?.entityName || props?.reviewIdNameData?.movableVehicle?.entity_name)}</span>
                            </div>
                        </div>

                        <div class="flex flex-wrap flex-row -mx-4 ml-12">
                            <div class="flex-shrink max-w-full px-4 w-full md:w-1/2 lg:w-1/4">
                                <p className={`${labelStyle}`}>GST No</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.movableVehicle?.gstNo || props?.reviewIdNameData?.movableVehicle?.gst_no)}</span>
                            </div>
                            <div class="flex-shrink max-w-full px-4 w-full md:w-1/2 lg:w-1/4">
                                <p className={`${labelStyle}`}>Vehicle No</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.movableVehicle?.vehicleNo || props?.reviewIdNameData?.movableVehicle?.vehicle_no)}</span>
                            </div>
                            <div class="flex-shrink max-w-full px-4 w-full md:w-1/2 lg:w-1/4">
                                <p className={`${labelStyle}`}>Vehicle Name</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.movableVehicle?.vehicleName || props?.reviewIdNameData?.movableVehicle?.vehicle_name)}</span>
                            </div>
                            <div class="flex-shrink max-w-full px-4 w-full md:w-1/2 lg:w-1/4">
                                <p className={`${labelStyle}`}>Vehicle Type</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.movableVehicle?.vehicleType || props?.reviewIdNameData?.movableVehicle?.vehicle_type)}</span>
                            </div>
                        </div>

                        <div class="flex flex-wrap flex-row -mx-4 ml-12">
                            <div class="flex-shrink max-w-full px-4 w-full md:w-1/2 lg:w-1/4">
                                <p className={`${labelStyle}`}>Brand in Vehicle</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.movableVehicle?.brandDisplayedInVehicle || props?.reviewIdNameData?.movableVehicle?.brand_display)}</span>
                            </div>
                            <div class="flex-shrink max-w-full px-4 w-full md:w-1/2 lg:w-1/4">
                                <p className={`${labelStyle}`}>Front Area(Sq ft)</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.movableVehicle?.frontArea || props?.reviewIdNameData?.movableVehicle?.front_area)}</span>
                            </div>
                            <div class="flex-shrink max-w-full px-4 w-full md:w-1/2 lg:w-1/4">
                                <p className={`${labelStyle}`}>Rear Area(Sq ft) </p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.movableVehicle?.rearArea || props?.reviewIdNameData?.movableVehicle?.rear_area)}</span>
                            </div>
                            <div class="flex-shrink max-w-full px-4 w-full md:w-1/2 lg:w-1/4">
                                <p className={`${labelStyle}`}>Side 1 Area(Sq ft)</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.movableVehicle?.sideOneArea || props?.reviewIdNameData?.movableVehicle?.side_area)}</span>
                            </div>
                        </div>

                        <div class="flex flex-wrap flex-row -mx-4 ml-12">
                            <div class="flex-shrink max-w-full px-4 w-full md:w-1/2 lg:w-1/4">
                                <p className={`${labelStyle}`}>Top Area(Sq ft)</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.movableVehicle?.topArea || props?.reviewIdNameData?.movableVehicle?.top_area)}</span>
                            </div>
                            <div class="flex-shrink max-w-full px-4 w-full md:w-1/2 lg:w-1/4">
                                <p className={`${labelStyle}`}>Display Type</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.movableVehicle?.displayType || props?.reviewIdNameData?.movableVehicle?.display_type)}</span>
                            </div>
                            <div class="flex-shrink max-w-full px-4 w-full md:w-1/2 lg:w-1/4">
                                <p className={`${labelStyle}`}>Trade License No</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.movableVehicle?.tradeLicenseNo || props?.reviewIdNameData?.movableVehicle?.trade_license_no)}</span>
                            </div>
                            <div class="flex-shrink max-w-full px-4 w-full md:w-1/2 lg:w-1/4">
                                <p className={`${labelStyle}`}>Typolgy Type</p>
                                <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.movableVehicle?.typology || props?.reviewIdNameData?.movableVehicle?.typology)}</span>
                            </div>
                        </div>


                    </div>
                    {/* document */}
                    <div className='col-span-4 border border-dashed border-violet-800 '>
                        <h1 className='text-center p-3 mb-2 text-lg bg-white text-gray-600 font-sans font-bold'>Documents Uploaded</h1>
                        <div className='grid grid-cols-1 md:grid-cols-1 lg:grid-cols-1 p-2 ml-12'>
                            {props?.reviewIdNameData?.movableVehicleDoc?.map((data, index) => (
                                <div className=''>
                                    <>
                                        <p className={`${labelStyle}`}>{data?.docCode} </p>
                                        <div className='flex -mt-2'>
                                            <span className={`${inputStyle} flex-1`}>{data?.image?.name}</span>
                                            <span className={`mt-4  flex-1 float-right`}><img src="https://cdn-icons-png.flaticon.com/512/1829/1829589.png" alt="Preview Image" className={`w-10 shadow-lg`} /></span>
                                        </div>
                                    </>
                                </div>
                            ))}

                        </div>
                    </div>
                </div>
                <div className="grid grid-cols-12 w-full p-3">
                    <div className='md:pl-0 col-span-6'>
                        <button type="button" class="text-xs py-2 px-4 inline-block text-center mb-3 rounded leading-5 text-gray-100 bg-indigo-500 border border-indigo-500 hover:text-white hover:bg-indigo-600 hover:ring-0 hover:border-indigo-600 focus:bg-indigo-600 focus:border-indigo-600 focus:outline-none focus:ring-0" onClick={() => setFormIndex(2)}>Back</button>

                    </div>
                    <div className='col-span-6'>
                        <button type="submit" class="float-right text-xs py-2 px-4 inline-block text-center mb-3 rounded leading-5 text-gray-100 bg-green-500 border border-green-500 hover:text-white hover:bg-green-600 hover:ring-0 hover:border-green-600 focus:bg-green-600 focus:border-green-600 focus:outline-none focus:ring-0" onClick={submitForm}>Submit</button>

                    </div>
                </div>
            </div>
        </>
    )
}

export default ReviewMovableApplication